<template>
  <q-page padding>
    <q-scroll-area style="height: 85vh;">
        <q-card>
            <q-card-section>
                <q-item>
                <q-item-section avatar>
                    <q-icon name="event" color="primary" />
                </q-item-section>
                <q-item-section>
                    <q-item-label>Datum und Uhrzeit</q-item-label>
                    <q-item-label caption>{{ formattedDate }}</q-item-label>
                </q-item-section>
                </q-item>

                <q-item>
                <q-item-section avatar>
                    <q-icon name="location_on" color="primary" />
                </q-item-section>
                <q-item-section>
                    <q-item-label>Standort</q-item-label>
                  <q-item-label caption>{{ location }}</q-item-label>
                </q-item-section>
                </q-item>

                <q-item>
                <q-item-section avatar>
                    <q-icon name="info" color="primary" />
                </q-item-section>
                <q-item-section>
                    <q-item-label>Buchungstyp</q-item-label>
                    <q-item-label caption>{{ bookingType }}</q-item-label>
                </q-item-section>
                </q-item>
            </q-card-section>
        </q-card>

      <q-stepper v-model="step" header-nav flat vertical animated>

        <q-step ref='step0' :name="0" title="Identifikation" :done="step > 0" :header-nav="step > 0">
            <q-form ref="cucumberForm" class="q-gutter-md">
              <q-input
                standout
                v-model="cucumberId"
                label="Cucumber ID"
                placeholder="Cucumber ID eingeben"
                :rules="[
                  val => !!val || 'Cucumber ID ist notwendig',
                  val => /^CC\d+[A-Z]$/.test(val) || 'Cucumber ID muss mit CC beginnen, gefolgt von Ziffern und einem Großbuchstaben'
                ]"
              >
              <template v-slot:prepend>
                <q-icon name="person" />
                </template>
                </q-input>

              <q-stepper-navigation>
                <q-btn no-caps @click="nextStep" color="primary" label="Weiter" />
              </q-stepper-navigation>

            </q-form>
        </q-step>

        <q-step ref='step1' :name="1" title="Dauer auswählen" :done="step > 1" :header-nav="step > 1">
            <q-form class="q-gutter-md">
              <q-select
                v-model="hours"
                dense
                :options="hoursOptions"
                label="Wieviele Stunden soll der Raum gebucht werden?"
                :rules="[val => !!val || 'Buchungsdauer ist notwendig']"
              >
                <template v-slot:option="scope">
                  <q-item v-bind="scope.itemProps">
                    <q-item-section>
                      <q-item-label>{{ scope.opt }} Stunde{{ scope.opt > 1 ? 'n' : '' }}</q-item-label>
                    </q-item-section>
                    <q-item-section side>
                      <q-item-label caption>{{ getEndTime(scope.opt) }}</q-item-label>
                    </q-item-section>
                  </q-item>
                </template>
              </q-select>

              <q-stepper-navigation>
                <q-btn no-caps @click="nextStep" color="primary" label="Weiter" />
              </q-stepper-navigation>

              <!--
              <q-btn no-caps label="Weiter" type="submit" color="primary"/>
            -->
            </q-form>
        </q-step>

        <q-step ref='step2' :name="2" title="Instrumente & Gäste auswählen" :done="step > 2" :header-nav="step > 2">
            <q-form  class="q-gutter-md">
              <q-select
                v-if="!isSingleBooker"
                v-model="guests"
                :options="guestsOptions"
                label="Anzahl der Gastmusiker:innen"
                :rules="[val => !!val || 'Anzahl der Gastmusiker:innen ist notwendig']"
              />
              <div>Welche Instrumente sollen freigeschaltet werden?</div>
              <q-checkbox
                v-for="instrument in supportedInstruments"
                :key="instrument"
                dense
                @click="resetInstrumentError"
                v-model="instruments[instrument]"
                :label="instrument"
              />
              <div v-if="showInstrumentError" class="text-negative">Bitte wählen Sie mindestens ein Instrument aus.</div>

              <q-stepper-navigation>
                <q-btn no-caps @click="nextStep" color="primary" label="Weiter" />
                <q-btn no-caps flat @click="step = 1" color="primary" label="Zurück" class="q-ml-sm" />
             </q-stepper-navigation>


             <!--
              <q-btn no-caps label="Weiter" type="submit" color="primary"/>
            -->
            </q-form>
        </q-step>

        <q-step ref='step3' :name="3" title="Überprüfen und Buchen" :done="step > 3" :header-nav="step > 3">
            <q-form class="q-gutter-md">
              <div>
                <q-item>
                  <q-item-section>
                    <q-item-label>Cucumber ID:</q-item-label>
                    <q-item-label caption>{{ cucumberId }}</q-item-label>
                  </q-item-section>
                </q-item>
                <q-item>
                  <q-item-section>
                    <q-item-label>Zeitraum:</q-item-label>
                    <q-item-label caption>{{ displayBookingSlot }}</q-item-label>
                  </q-item-section>
                </q-item>
                <q-item v-if="!isSingleBooker">
                  <q-item-section>
                    <q-item-label>Anzahl der Gastmusiker:innen:</q-item-label>
                    <q-item-label caption>{{ guests }}</q-item-label>
                  </q-item-section>
                </q-item>
                <q-item>
                  <q-item-section>
                    <q-item-label>Instrumente:</q-item-label>
                    <q-item-label caption>{{ selectedInstruments }}</q-item-label>
                  </q-item-section>
                </q-item>
                <q-item>
                  <q-item-section>
                    <q-item-label>Preis:</q-item-label>
                    <q-item-label caption>{{ displayTotalPrice }}</q-item-label>
                  </q-item-section>
                </q-item>
              </div>

              <q-stepper-navigation>
                <q-btn no-caps @click="onSubmit" color="primary" label="Verbindlich buchen" />
                <q-btn no-caps flat @click="step = 2" color="primary" label="Zurück" class="q-ml-sm" />
             </q-stepper-navigation>

             <div v-if="submissionError" class="text-negative">
              {{ errorMessage }}
             </div>

             <!--
              <q-btn no-caps label="Verbindlich buchen" type="submit" color="primary"/>
            -->
            </q-form>
        </q-step>

        <q-step ref='step4' :name="4" title="Nächste Schritte" :header-nav="step > 4">
            <div>
              <p>Vielen Dank für Ihre Buchung!</p>
              <p>Im nächsten Schritt erhalten Sie von uns eine E-Mail mit der Bestätigung und den Rechnungsdaten.</p>
              <q-btn no-caps label="Zur Startseite" to="/" color="primary"/>
            </div>
        </q-step>
      </q-stepper>
    </q-scroll-area>
  </q-page>
</template>

<script>
import { nextTick, ref, computed, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import { srvApi } from 'src/boot/axios'
import { useQuasar } from 'quasar'

export default {
  name: 'BookingRequest',
  setup() {
    const $q = useQuasar()

    const cucumberForm = ref(null);
    const step0 = ref(null)
    const step1 = ref(null)
    const step2 = ref(null)
    const step3 = ref(null)
    const step4 = ref(null)

    // get data from router
    const route = useRoute()
    const bookingDate = route.params.bookingDate
    const bookingHour = parseInt(route.params.bookingHour)
    const isSingleBooker = ref(route.params.isSingleBooker === 'true')

    // get data from rest api during preparation
    const maxAvailableHours = ref(1)
    const location = ref('')
    const maxCapacity = ref(1)
    const hourlyRates = ref([])
    const dailyRate = ref(9999)
    const supportedInstruments = ref([])
    const requestDate = ref('')

    // form data
    const cucumberId = ref('')
    const hours = ref(1)
    const guests = ref( isSingleBooker.value ? 0 : 1)
    const instruments = ref({})

    // state variables
    const step = ref(0)
    const invalidBookingRequest = ref(false)
    const submissionError = ref(false)
    const showInstrumentError = ref(false)
    const errorMessage = ref('')

    const formattedDate = computed(() => {
        // use date and time
        let date = new Date(bookingDate)
        date.setHours(bookingHour)
        const time = date.toLocaleTimeString('de-DE', { hour: '2-digit', minute: '2-digit' })
        // include weekday short name
        const weekday = date.toLocaleDateString('de-DE', { weekday: 'short' })
        return `${weekday}, ${date.toLocaleDateString('de-DE')} ${time} Uhr`
    })

    const showErrorNotify = (message) => {
      $q.notify({
        color: 'negative',
        message: message,
        icon: 'report_problem'
      })
    }

    const showPositiveNotify = (message) => {
      $q.notify({
        color: 'positive',
        message: message,
        icon: 'check_circle'
      })
    }

    const bookingType = computed(() => {
      return isSingleBooker.value ? 'Einzelbucher:inn' : 'Band (2 - 5 Personen)'
    })

    const displayTotalPrice = computed(() => {
        const totalPrice = formatPrice(getTotalPrice(bookingHour, hours.value))

        return totalPrice;
    })

    const displayBookingSlot = computed(() => {
        const startHour = bookingHour
        const duration = hours.value
        const endHour = startHour + duration
        const date = formattedDate.value

        return `${date} - ${endHour} Uhr`
    })

    const hoursOptions = computed(() => {
      return Array.from({ length: maxAvailableHours.value }, (_, i) => i + 1)
    })

    const guestsOptions = computed(() => {
        const maxGuestCapacity = maxCapacity.value - 1
        return Array.from({ length: maxGuestCapacity }, (_, i) => i + 1)
    })

    const resetInstrumentError = () => {
      showInstrumentError.value = false
    }

    const isFormValid = computed(() => {
      return cucumberId.value &&
             hours.value &&
             (isSingleBooker.value || guests.value) &&
             Object.values(instruments.value).some(v => v)
    })


    const nextStep = async () => {
      if (step.value === 0) {
        if (cucumberForm.value) {
            const isValid = await cucumberForm.value.validate()
            if (!isValid) {
                return
            }
        } else {
            if (!cucumberId.value) {
                return
            }
        }
      }
      if (step.value === 2 && !isFormValid.value) {
        showInstrumentError.value = !Object.values(instruments.value).some(v => v)
        return
      }

       // Scroll to the next step
       nextTick(() => {
        const nextStepRef = [step0, step1, step2, step3, step4][step.value - 1]
        if (nextStepRef.value) {
          nextStepRef.value.$el.scrollIntoView({ behavior: 'smooth' })
        }
      })

      step.value++
    }

    const onSubmit = async () => {
        console.log('onSubmit', isFormValid.value)
      if (!isFormValid.value) {
        showInstrumentError.value = !Object.values(instruments.value).some(v => v)
        return
      }

      try {
        const requestedPrice = getTotalPrice(bookingHour, hours.value)
        const response = await srvApi.post('/api/v1/bookings', {
          cucumberId: cucumberId.value,
          hours: hours.value,
          guests: guests.value,
          instruments: instruments.value,
          date: bookingDate,
          hour: bookingHour,
          isSingleBooker: isSingleBooker.value,
          requestedPrice: requestedPrice,
          requestDate: requestDate.value
        })
        console.log('Booking submitted successfully', response.data)
        step.value++
        submissionError.value = false
        errorMessage.value = ''

        showPositiveNotify('Ihre Buchung wurde erfolgreich eingerichtet.')
      } catch (error) {
        submissionError.value = true
        console.error('Error submitting booking', error)
        
        if (error.response) {
          console.log('Error response:', error.response)
          const status = error.response.status
          const detail = error.response.data.detail

          if (status === 403) {
            errorMessage.value = 'Ungültige Cucumber ID. Sie sind nicht berechtigt, eine Buchung vorzunehmen.'
          } else if (status === 400) {
            if (detail.includes("Booking already exists")) {
              errorMessage.value = 'Dieser Buchungsslot ist nicht mehr verfügbar. Bitte wählen Sie einen anderen Zeitraum.'
            } else {
              errorMessage.value = detail || 'Ein Fehler ist aufgetreten. Bitte versuchen Sie es erneut.'
            }
          } else if (status === 500) {
            errorMessage.value = 'Ein unerwarteter Fehler ist aufgetreten. Bitte versuchen Sie es später erneut.'
          } else {
            errorMessage.value = `Fehler ${status}: ${detail || 'Unbekannter Fehler'}`
          }
        } else {
          errorMessage.value = 'Ein Netzwerkfehler ist aufgetreten. Bitte überprüfen Sie Ihre Internetverbindung.'
        }

        showErrorNotify(errorMessage.value)

      }
    }

    const prepareBooking = async () => {
      try {
        const response = await srvApi.get('/api/v1/bookings/prepare', {
          params: {
            date: bookingDate,
            hour: bookingHour,
            single_user: isSingleBooker.value
          }
        })
        maxAvailableHours.value = response.data.max_consecutive_hours
        location.value = response.data.location
        supportedInstruments.value = response.data.supported_instruments
        maxCapacity.value = response.data.max_capacity
        dailyRate.value = response.data.daily_rate
        requestDate.value = response.data.request_date
        instruments.value = Object.fromEntries(
          response.data.supported_instruments.map(instrument => [instrument, false])
        )
        hourlyRates.value = response.data.hourly_rates
      } catch (error) {
        invalidBookingRequest.value = true;
        console.error('Error preparing booking', error)
      }
    }

    const getTotalPrice = (startHour, duration) => {
      let totalPrice = 0
      for (let i = startHour; i <= startHour + duration - 1; i++) {
          const hourlyRate = hourlyRates.value.find(rate => rate.hour === i)
          totalPrice += hourlyRate.rate
      }

      if (totalPrice > dailyRate.value) {
        totalPrice = dailyRate.value
      }

      return totalPrice
    }

    const getEndTime = (hours) => {
      const currentHour = bookingHour + hours - 1
      const startDate = new Date(bookingDate)
      startDate.setHours(bookingHour)
      const endDate = new Date(startDate.getTime() + hours * 60 * 60 * 1000)
      const endDateString = endDate.toLocaleTimeString('de-DE', { hour: '2-digit', minute: '2-digit' })

      const totalPrice = formatPrice(getTotalPrice(bookingHour, hours))

      const endString = `für ${totalPrice} bis ${endDateString} Uhr`
      return endString
    }

    const formatPrice = (price) => {
      return price.toFixed(2).replace('.', ',') + ' €'
    }

    const resetForm = () => {
      step.value = 0
      cucumberId.value = ''
      hours.value = 1
      guests.value = isSingleBooker.value ? 0 : 1
      instruments.value = {}
      showInstrumentError.value = false
    }

    const selectedInstruments = computed(() => {
      return Object.keys(instruments.value).filter(key => instruments.value[key]).join(', ')
    })

    onMounted(prepareBooking)

    return {
      step,
      displayTotalPrice,
      displayBookingSlot,
      cucumberId,
      hours,
      guests,
      instruments,
      formattedDate,
      bookingType,
      hoursOptions,
      guestsOptions,
      onSubmit,
      submissionError,
      showPositiveNotify,
      isSingleBooker,
      showInstrumentError,
      resetInstrumentError,
      location,
      maxAvailableHours,
      getEndTime,
      invalidBookingRequest,
      showErrorNotify,
      supportedInstruments,
      formatPrice,
      dailyRate,
      nextStep,
      resetForm,
      getTotalPrice,
      requestDate,
      selectedInstruments,
      cucumberForm,
      step0,
      step1,
      step2,
      step3,
      step4,
      errorMessage
    }
  }
}
</script>